body {
  margin: 0;
  font-family: Neucha, sans-serif;
}
p {
  font-size: 24px;
}
h1 {
  font-size: 48px;
}
h2 {
  font-size: 32px;
}
h3 {
  font-size: 26px;
}
.container {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.left,
.right {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.left {
  height: 100vh;
  flex: 4;
  justify-content: space-evenly;
  position: relative;
  background-color: #aed3e1;
  color: #333;
}
.left-side_img {
  position: absolute;
  left: 0;
  top: 55vh;
  object-fit: contain;
  width: 200px;
}
.right {
  color: #333;
  background-color: #d0f7d9;
  height: 100vh;
  justify-content: space-evenly;
  text-align: center;
}
.math {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.number,
.symbol {
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 24px;
}
.number,
button {
  text-transform: capitalize;
  align-self: center;
  background-color: #fff;
  background-image: none;
  background-position: 0 90%;
  background-repeat: repeat no-repeat;
  background-size: 4px 3px;
  border-radius: 15px 225px 255px 15px 15px 255px 225px 15px;
  border-style: solid;
  border-width: 2px;
  box-shadow: rgba(0, 0, 0, 0.2) 15px 28px 25px -18px;
  box-sizing: border-box;
  color: #41403e;
  cursor: pointer;
  display: inline-block;
  font-family: Neucha, sans-serif;
  font-size: 1rem;
  line-height: 23px;
  outline: none;
  padding: 0.75rem;
  text-decoration: none;
  transition: all 235ms ease-in-out;
  border-bottom-left-radius: 15px 255px;
  border-bottom-right-radius: 225px 15px;
  border-top-left-radius: 255px 15px;
  border-top-right-radius: 15px 225px;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  text-align: center;
}
button {
  width: 100px;
  margin: 0 50px;
}
input {
  text-align: center;
}
.result-img {
  height: 200px;
  width: 200px;
}
.right-side_img {
  object-fit: contain;
  height: 300px;
}
button:hover {
  box-shadow: rgba(0, 0, 0, 0.3) 2px 8px 8px -5px;
  transform: translate3d(0, 2px, 0);
}

button:focus {
  box-shadow: rgba(0, 0, 0, 0.3) 2px 8px 4px -6px;
}
