@media screen and (max-width: 768px) {
  .right-side_img {
    width: 170px;
  }
  .left-side_img {
    display: none;
  }
  .number,
  button {
    margin: 5px;
  }
}
@media screen and (max-width: 426px) {
  h1 {
    font-size: 40px;
    margin: 20px 0;
  }
  h2 {
    font-size: 24px;
  }
  .container {
    flex-direction: column;
    text-align: center;
  }
  .left {
    height: 90vh;
    width: 100vw;
    justify-content: center;
  }
  .right {
    height: max-content;
    min-height: 50vh;
    width: 100vw;
    justify-content: center;
  }
  .hidden {
    display: none;
  }
  .result-img {
    display: none;
  }
  .left-side_img,
  .right-side_img {
    display: none;
  }
  .math {
    width: 90vw;
  }
  .number,
  button {
    transform: scale(0.8);
    margin: 0;
  }
}
